.header-body{
    margin: 0px;
    justify-content: space-around;
    display: flex;
    background: rgb(43, 43, 43);
    width: 100%;
}
.header-body .mainLinks{
    display: flex;
    margin: 10px;
}
.mainLinks .logo{
    font-family: 'Fugaz One', cursive;
    font-size: 29px;
    margin: 0px;
    color: #fff;
}
.mainLinks .logo span{
    color: blue;
}
.mainLinks .Links{
    display: flex;
    margin: 0px;
    align-items: center;
}
.mainLinks .Links .subLinks{
    font-size: 14px;
    list-style: none;
    color: #fff;
    padding: 7px 15px;
    margin-left: 25px;
    cursor: pointer;
    box-shadow: 0px 0px 5px 1px black;
    border-radius: 14px;
}

.mainLinks .Links .subLinks:hover{
    background: rgb(43, 43, 43);
    color: blue;
}
.menu-toggle{
    display: none;
}
.header-body .searchNotification{
    display: flex;
    margin: 10px;
    align-items: center;
    justify-content: space-around;
}
.header-body .searchNotification .headerIcons{
    font-size: 20px;
    color: #888;
    margin-left: 20px;
    padding: 0px;
}
.searchIcons{
    display: none;
}
.searchNotification .wordSearch{
    margin-right: 10px;
    margin-left: 10px;
}
.header-body .searchNotification input{
    width: 230px;
    padding: 7px;
    border-width: 0px;
    background: rgb(43, 43, 43);
    border: 1px solid #888;
    border-right: 0px;
    box-shadow: 0px 0px 5px 1px black;
}
.header-body .searchNotification button{
    padding: 7px;
    border-width: 0px;
    border: 1px solid #888;
    border-left: 0px;
    background: rgb(78, 78, 78);
    box-shadow: 0px 0px 5px 1px black;
}
@media only screen and (max-width: 950px){
    .wordSearch{
        display: none;
    }
}


@media only screen and (max-width: 600px){
    .header-body{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .mainLinks .Links{
        display: none;
    }
    .menu-toggle{
        display: flex;
        max-height: 40px;
    }
    #dropdown-basic{
        background: #111;
        border-radius: 8px;
        border-width: 0px;
        box-shadow: 0px 0px 20px #000;
        margin-right: 20px;
        outline: none;
    }
    .menu-mobile{
        margin: 0px;
        padding: 0px;
        display: flex;
        align-items: center;
    }
    .menu-mobile h2{
        margin: 0px;
        padding: 5px;
        padding-right: 0px;
        font-size: 17px;
        color: #ddd;
    }
    .menu-mobile .menuIcons{
        color: #ddd;
        padding: 5px;
        margin: 0px;
    }
    .dropdowns{
        background: none;
    } 
    .mobile-menu-links{
        display: block;
        list-style: none;
        color: #f3f3f3;
        background: #111;
        margin: 0px;
        margin-right: -20px;
        border-radius: 10px;
        box-shadow: 0px 0px 20px 0px #000;
    }
    .mobile-menu-links li{
        text-align: right;
        padding: 13px;
    }
    .headerIcons{
        display: none;
    }
    .menuIcon{
        color: #ddd;
        padding-right: 10px;
    }
}
.mobileSearch{
    display: none;
}
.Welcome-section{
    background: url('https://res.cloudinary.com/dmixz7eur/image/upload/v1598184844/sa_q6nsrl.jpg');
    background-position: center;
    background-size: cover;
    width: 100%;
    margin: 0%;
}
.WelcomeGreetings{
    width: 100%;
    margin: 0px;
    padding: 0px;
    justify-content: center;
}
.WelcomeDiv{
    width: 100%;
    margin: 100px 0px;
}
.WelcomeDiv h1{
    font-family: 'Alata', sans-serif;
    font-size: 50px;
    font-weight: 800;
    color: #ddd;
    padding: 20px;
    margin-top: 30px;
}
.WelcomeDiv h1 span{
    color: blue;
}

.WelcomeDiv p{
    font-size: 15px;
    color: #f3f3f3;
    font-family: 'Roboto', sans-serif;
    font-weight: 200;
    line-height: 30px;
    padding: 20px;
    padding-top: 0px;
}
.WelcomeDiv .WelcomeReadMore{
    border-width: 0px;
    background: blue;
    box-shadow: 0px 0px 25px 0px black;
    color: #ddd;
    font-weight: 600;
    font-size: 16px;
    padding: 10px 30px;
    border-radius: 10px;
    margin-bottom: 20px;
}
.WelcomeImage{
    margin: 0px;
    padding: 0px;
    width: 100%;
    background: url('https://res.cloudinary.com/dmixz7eur/image/upload/v1598348133/13011-removebg-preview_bjjxzd.png');
    background-size: cover;
    background-position-x: center;
}
.WelcomeImage img{
    width: 100%;
    height: auto;
}

@media only screen and (max-width: 1000px){
    .mobileSearch{
        margin-top: 0px;
        display: inline;
    }
    .mobileSearch .mobileInput{
        margin-top: 20px;
        padding: 10px;
        border-radius: 12px;
        border-top-right-radius: 0px;
        border-bottom-right-radius: 0px;
        border-width: 0px;
        width: 260px;
        background: #111;
        box-shadow: 0px 0px 25px 0px black;
        color: #f3f3f3;
    }
    .mobileSearch .mobileSearchButton{
        margin-top: 20px;
        padding: 10px;
        border-radius: 12px;
        border-top-left-radius: 0px;
        border-bottom-left-radius: 0px;
        background: blue;
        color: #f3f3f3;
        border-width: 0px;
    }
    .WelcomeDiv h1{
        font-family: 'Alata', sans-serif;
        font-size: 50px;
        font-weight: 800;
        color: #ddd;
        padding: 20px;
        margin-top: 30px;
    }
    .WelcomeDiv h1 span{
        color: blue;
    }
    .WelcomeDiv p{
        font-size: 15px;
        color: #f3f3f3;
        font-family: 'Roboto', sans-serif;
        font-weight: 200;
        line-height: 30px;
        padding: 20px;
        padding-top: 0px;
    }
    .WelcomeDiv .WelcomeReadMore{
        border-width: 0px;
        background: blue;
        box-shadow: 0px 0px 25px 0px black;
        color: #ddd;
        font-weight: 600;
        font-size: 16px;
        padding: 10px 30px;
        border-radius: 10px;
        margin-bottom: 20px;
    }
    .WelcomeDiv{
        margin: 10px 0px;
    }
    .WelcomeImage{
        display: flex;
    }
}
.tabComponents{
    background: #111;
    margin: 0px;
    padding: 0px;
}
.Tabs .tabComponents button{
    padding: 8px 14px;
    border-width: 0px;
    background: #111;
    color: #ddd;
    font-size: 17px;
    outline: none;
}
.Tabs .tabComponents button:hover{
    border-bottom: 4px solid blue;
    color: blue;
}

.Tabs .tabComponents button.active{
    border-bottom: 4px solid blue;
    color: blue;
}
.myActive{
    background: #fff;
}
.searchFilter{
    width: 100%;
    display: flex;
    justify-content: left;
    background: #f3f3f3;
    box-shadow: 0px 1px 3px #888;
    align-items: center;
}
.searchFilter h3{
    font-size: 18px;
    color: orangered;
    padding: 14px;
    padding-left: 30px;
}

.searchFilter .filter1{
    background: #fff;
    display: flex;
    align-items: center;
    padding: 5px 12px;
    cursor: pointer;
    border-radius: 10px;
    margin: 0px 10px;
    box-shadow: 0px 4px 15px 1px rgb(175, 175, 175);
}
.searchFilter .filter2{
    margin: 0px 10px;
    cursor: pointer;
    background: #fff;
    display: flex;
    align-items: center;
    padding: 5px 12px;
    border-radius: 10px;
    box-shadow: 0px 4px 15px 1px rgb(175, 175, 175);
}
.searchFilter .filter3{
    background: #fff;
    margin: 0px 10px;
    display: flex;
    cursor: pointer;
    align-items: center;
    padding: 5px 12px;
    border-radius: 10px;
    box-shadow: 0px 4px 15px 1px rgb(175, 175, 175);
}
.searchFilter h4{
    font-size: 15px;
    padding: 5px;
    margin: 0px;
}
@media only screen and (max-width: 700px){
    .searchFilter{
        width: 100%;
        display: flex;
    }
    .searchFilter h3{
        font-size: 13px;
    }
    .searchFilter .filter1, .searchFilter .filter2, .searchFilter .filter3{
        padding: 2px 5px;
    }
    .searchFilter .filter1 h4, .searchFilter .filter2 h4, .searchFilter .filter3 h4{
        font-size: 13px;
    }
}
.set{
    width: 100%;
    margin: 0%;
}
.reviews{
    width: 100%;
    margin: 0px;
    padding: 0px;
    justify-content: space-around;
    align-items: center;
    /* background: url('https://res.cloudinary.com/dmixz7eur/image/upload/v1598284223/bg1_ebqhzz.jpg'); */
    background-color: #f3f3f3;
    margin-top: 2px;
}
.reviews .reviewRow{
    justify-content: space-around;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}
.reviews .reviewCol{
    justify-content: center;
    display: flex;
}
.reviews .bestQuality{
    margin: 20px;
    width: 300px;
    min-width: 300px;
    max-width: 310px;
    background: #fff;
    border-radius: 5px;
    box-shadow: 0px 0px 10px #888;
}
.reviews .bestQuality:hover{
    cursor: pointer;
    box-shadow: 0px 0px 30px #888;
}
.reviews .bestQuality h2{
    font-family: 'Righteous', cursive;
    font-size: 25px;
    color: blue;
}
.reviews .bestQuality img{
    max-width: 200px;
    max-height: 150px;
    margin: 0px;
    padding: 20px;
}
.reviews p {
    display: flex;
    justify-content: center;
    text-align: center;
    padding: 25px;
    font-size: 14px;
    font-weight: 600;
    line-height: 17px;
    padding-top: 8px;
    padding-bottom: 30px;
    color: rgb(97, 97, 97);
}
.reviews p .sendIcon{
    color: blue;
}
.slides .slick-prev, .slides .slick-next{
    position: absolute;
    z-index: 1;
    top: 50%;
}
.RevDiv{
    background: #f3f3f3;
    margin: 0%;
    width: 100%;
}
.RevDiv .MyReviews{
    color: #111;
    font-family: 'Righteous', cursive;
    font-size: 45px;
    padding: 20px;
}
.slides{
    background: #f3f3f3;
    position: relative;
    display: flex;
    width: 100%;
    justify-content: space-around;
    flex-wrap: wrap;
}

.slides .col{
    background: #fff;
    width: 250px;
    min-width: 250px;
    max-width: 260px;
    margin: 20px;
    height: 400px;
    text-align: center;
    box-shadow: 0px 0px 3px 0px #888;
}
.slides .col:hover{
    box-shadow: 0px 0px 13px 0px #888;
}
.slides img{
    margin: auto;
    background: black;
    border-radius: 50%;
    border: 4px solid blue;
    max-width: 100px;
    margin-top: 20px;
    margin-bottom: 10px;
}
.slides .col h1{
    font-size: 18px;
    font-family: 'Righteous', cursive;
}
.reviewIcons{
    color: #fecb00;
}
.slides .col p{
    font-family: 'Roboto', sans-serif;
    font-size: 13px;
    padding: 20px 25px;
}
@media only screen and (max-width: 1000px){
    .slides .slidesPerRow {

    }
}
.Jobs{
    background-color: rgb(37, 37, 37);
    margin: 0px;
    border-top: 7px solid darkblue;
    justify-content: center;
    width: 100%;
    border-bottom: 7px solid darkblue;
}
.Jobs .JobsServices{
    justify-content: center;
    margin: 0px;
}
.Jobs .JobsServices h1{
    font-size: 45px;
    margin: 20px;
    color: #fff;
    font-family: 'Righteous', cursive;
}
.Jobs .JobRow{
    justify-content: space-around;
    display: flex;
    width: 100%;
}
.Jobs .JobRow .JobCol{
    justify-content: center;
    background-color: #111;
    margin: 30px;
    border-radius: 12px;
    box-shadow: 0px 0px 20px 0px rgb(26, 26, 26);
    height: 300px;
    min-width: 200px;
    width: 200px;
    max-width: 210px;
    overflow-y: hidden;
}
.Jobs .JobRow .JobCol:hover{
    box-shadow: 0px 0px 20px 0px rgb(26, 26, 26);
    cursor: pointer;
}
.Jobs .JobRow .JobCol img{
    width: 120px;
    height: 100px;
}
.Jobs .JobRow .JobCol h1{
    font-size: 18px;
    font-family: 'Righteous', cursive;
    color: blue;
}
.Jobs .JobRow .JobCol .JobAbout{
    display: flex;
    margin-right: 10px;
}
.Jobs .JobRow .JobCol .JobAbout .headerIcons{
    color: blue;
}
.Jobs .JobRow .JobCol .JobAbout p{
    color: rgb(177, 177, 177);
    font-size: 13px;
    line-height: 15px;
    font-weight: 300;
}
.FooterSection{
    margin: 0px;
    /* justify-content: center; */
    background: rgb(26, 26, 26);
}
.FooterSection .footerRow{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin: auto;
}
.footerRow .footerCol{
    margin: auto;
    margin-top: 20px;
    text-align: left;
    width: 250px;
    min-width: 250px;
    max-width: 260px;
}
.footerRow .footerCol h1{
    font-size: 28px;
    font-family: 'Righteous', cursive;
    color: #ddd;
}
.footerRow .footerCol ul{
    padding: 0px;
    margin: 0px;
    list-style: none;
}
.footerRow .footerCol ul li{
    color: rgb(161, 161, 161);
    cursor: pointer;
    margin-top: 10px;
}
.footerRow .footerCol ul li:hover{
    color: blue;
}
.footerRow .footerCol label{
    font-size: 12px;
    color: rgb(161, 161, 161);
}
.footerRow .footerCol input{
    background: rgb(26, 26, 26);
    border: 1px solid #888;
    padding: 6px;
    width: 100%;
    outline: none;
}
.footerRow .footerCol textarea{
    width: 100%;
    height: 120px;
    border: 1px solid #888;
    background: rgb(26, 26, 26);
    outline: none;
}
.footerRow .footerCol button{
    padding: 6px;
    width: 100px;
    background: rgb(39, 39, 39);
    border-radius: 20px;
    color: #fff;
    box-shadow: 0px 0px 10px 3px black;
}
.footerRow .footerCol button:hover{
    color: blue;
    outline: none;
}
.footerRow .footerCol h2{
    color: #ddd;
    font-size: 16px;
    text-align: center;
    margin: 10px;
}
.footerRow .footerCol .footerSocialMedia{
    margin: 10px 10px;
    display: flex;
    justify-content: space-around;
}
.footerRow .footerCol .footerSocialMedia img{
    width: 30px;
    cursor: pointer;
}
